import jwt_decode from 'jwt-decode';
import {createContext, useReducer} from "react";
import {useHistory, Redirect} from "react-router-dom";
import React from 'react';
import moment from "moment";

export const authenticate = (token, cb) => {
    if (typeof window !== 'undefined') {
        console.log(token)
        localStorage.setItem('jwt', JSON.stringify(token));
        cb();
    }
}

export const isAuthenticated = () => {
    if (typeof window === 'undefined') return false;
    if (localStorage.getItem('jwt')) {
        const {exp, role} = jwt_decode(JSON.parse(localStorage.getItem('jwt')));

        if (role === "superadmin" || role === "admin") {
            console.log("ok")
            if ((new Date()).getTime() < exp * 1000) {
                return true;
            } else {
                localStorage.removeItem('jwt');
                checkLoggedInUser()
                return false
            }
        } else{
            localStorage.clear()
            console.log(role)
            checkLoggedInUser()
        }
    } else return false;
}


export const userInfo = () => {
    const jwt = JSON.parse(localStorage.getItem('jwt'));
    const decoded = jwt_decode(jwt);
    return {...decoded, token: jwt}
}
const checkLoggedInUser = () => {
    if (typeof window !== "undefined" || !localStorage.getItem("jwt")) {
        const route = location.pathname
        const history = useHistory();
        history.push("/");
        if (route !== '/') {
            location.reload()
        }
    }
}

const initialstate = {
    user: null,
};
if (localStorage.getItem("jwt")) {
    const jwt_Token_decoded = jwt_decode(localStorage.getItem("jwt"));
    // console.log(jwt_Token_decoded)
    // console.log(jwt_Token_decoded.exp * 1000);
    console.log(Date.now());
    if (!jwt_Token_decoded.flag) {
        localStorage.clear()
        checkLoggedInUser()
    }
    if (jwt_Token_decoded.expire * 1000 < Date.now()) {
        localStorage.clear(); // this runs only when I refresh the page or reload on route change it dosent work
        checkLoggedInUser()
    } else {
        initialstate.user = jwt_Token_decoded;
    }
}


const AuthContext = createContext({
    user: null,
    login: (userData) => {
    },
    logout: () => {
    },
});
const AuthReducer = (state, action) => {
    switch (action.type) {
        case "LOGIN":
            return {
                ...state,
                user: action.payload,
            };
        case "LOGOUT":
            return {
                ...state,
                user: null,
            };
        default:
            return state;
    }
};

    const clearCacheData = () => {
        const timer = localStorage.getItem("timer")
        if (timer) {
            if (moment(parseInt(timer)).add(6, "hours").unix() > moment().unix()){
                cleaner()
                localStorage.setItem("timer", Date.now())
            }
        }
        else {
            localStorage.setItem("timer", Date.now())
        }
    };

    const cleaner = () => {
        caches.keys().then((names) => {
            names.forEach((name) => {
                caches.delete(name).then(r =>  alert('Complete Cache Cleared'));
            });
        });
    }

export const AuthProvider = (props) => {
    checkLoggedInUser()
    clearCacheData()
    const [state, dispatch] = useReducer(AuthReducer, initialstate);
    const login = (userData) => {
        localStorage.setItem("jwt", userData.token);
        dispatch({
            type: "LOGIN",
            payload: userData,
        });
    };
    const logout = () => {
        localStorage.clear();
        dispatch({action: "LOGOUT"});
    };

    return (
        <AuthContext.Provider
            value={{user: state.user, login, logout}}
            {...props}
        />
    );
};

export {AuthContext};